import { Component } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ModalController } from '@ionic/angular';
import {VentasPage} from  './../ventas/ventas.page'
import {ConfigService} from './../config.service'
@Component({
  selector: 'app-tab3',
  templateUrl: 'tab3.page.html',
  styleUrls: ['tab3.page.scss']
})
export class Tab3Page {
  public items;
  public date1;
  public date2;
  constructor(private host:ConfigService,private http: HttpClient,private modal: ModalController) { 
      this.items=[]
      this.http.get<any>(this.host.getHost() + '/selectVentas').subscribe(data => {
        this.items = data;
      })
      this.date2 = Date();
      this.date1 = Date();
  }
  deleteVenta(item){
    this.http.post<any>(this.host.getHost() + '/deleteVentas',item).subscribe(data => {
      this.http.get<any>(this.host.getHost() + '/selectVentas').subscribe(data => {
        this.items = data;
      })
    })
  }
  searchDate(){
    var dateone = new Date(this.date1)
    var datetwo = new Date(this.date2)
    dateone.setDate(dateone.getDate() -1);
    datetwo.setDate(datetwo.getDate() +1);
    this.http.post<any>(this.host.getHost() + '/selectVentasForDate',{date1:dateone,date2:datetwo}).subscribe(data => {
      this.items = data;
    })
  }
  async Report() {
    const modal = await this.modal.create({
      component: VentasPage,
    });
    return await modal.present();
  }
}
